import {Component, Host, h, Prop, Element} from '@stencil/core';

@Component({
  tag: 'storm-collapse',
  styleUrl: 'storm-collapse.css',
  shadow: false,
})

export class StormCollapse {

  @Element() host: HTMLElement;

  @Prop() aClass: string = "btn btn-primary";
  @Prop() buttonName: string;
  @Prop() for: string = "collapseExample";


  getHref = () =>{
    let s = '#'
    s += this.for;
    return s;
  }



  render() {
    return (
      <Host>
        <a class={this.aClass} data-bs-toggle="collapse" href={this.getHref()} role="button" aria-expanded="false" aria-controls={this.for}>
          {this.buttonName}
        </a>
      </Host>
    );
  }

}
